import React from 'react';

export class LandingForm extends React.Component {
    
    constructor(props) {
        super(props);

        this.state = {
            isStarted: false
        }

        this.startHandler = this.startHandler.bind(this);
    }

    startHandler() {
        this.setState({
            isStarted: true
        })
    }
    
    render() {
        return (
            !this.state.isStarted ?

            <button className='button' onClick={this.startHandler}>Book Now</button>
            :
            <div>
                <div className="form-title">Personal information</div>
                <form>
                    <label htmlFor="first-name">Firs Name</label>
                    <input id="first-name" type="text"/>
                    <label htmlFor="last-name">Last Name</label>
                    <input id="last-name" type="text"/>
                    <label htmlFor="email">Email Address</label>
                    <input id="email" type="email"/>
                </form>
                <div className="form-title">Shipping Information</div>
                <form>
                    <label htmlFor="address">Address</label>
                    <input id="address" type="text"/>
                    <label htmlFor="country">Country</label>
                    <input id="country" type="text"/>
                    <label htmlFor="city">City</label>
                    <input id="city" type="text"/>
                    <label htmlFor="zip">Zip Code</label>
                    <input id="zip" type="text"/>
                </form>
                <div className="text">
                    <p>Thank you for your interest in the <span className="engel">ENGEL</span> bottle!</p>
                    <p>Once your <span className="engel">ENGEL</span> has been produced we will notify you via e-mail. You will have one week topurchase it. No payments required at the moment. Once you recived the mail that you bottle is ready - we kindly ask you to purchase it within 7 days. In case you are not interested anymore please click the NO INTEREST ANYMORE button in the confirmation mail.</p>
                </div>
                <form className="accept">
                    <div>
                        <input id="privacy" type="checkbox"/>
                        <label htmlFor="privacy">I have read and I confirm with the <a href={require('../../files/Privacy Policy engel.pdf')} target="_blank">privacy agreement</a>.</label>
                    </div>
                    <div>
                        <input id="purchase" type="checkbox"/>
                        <label htmlFor="purchase">I confirm that I want to reserve my <span className="engel">ENGEL</span> bottle for a later purchase.</label>
                    </div>
                </form>
                <button className='button' onClick={this.startHandler}>Order</button>
            </div>
        );
    }
}